
#app {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  background: #e6ebf0;
  min-width: 1200px;
}
.el-submenu [class^=fa] {
  vertical-align: baseline;
  margin-right: 10px;
}
.el-menu-item [class^=fa] {
  vertical-align: baseline;
  margin-right: 10px;
}
.toolbar {
  background: #ffffff;
  padding: 10px;
  margin-bottom: 20px;
  margin-top: 0px;
  padding-bottom: 0px;
}
.toolbar .el-form-item {
    margin-bottom: 10px;
}
.fade-enter-active,
.fade-leave-active {
  transition: all .2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}



